import { Component, Input, OnInit } from '@angular/core';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsSelectDropdownComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-select/ps-select-dropdown/ps-select-dropdown.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IchangeValues, IOptionsPsSelectDropdown, IPsSelect } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from './../../../../commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsDropdownproductCategoriesExposed } from './ps-dropdown-product-category.component.interfaces';

/**
 * @author G.Radwan
 * @since 02/03/2021
 *
 * <p> PsDropdownproductCategoriesComponent will fetch the list of product categories from server</p>
 */
@Component({
  selector: 'ps-dropdown-product-category',
  templateUrl: './ps-dropdown-product-category.component.html',
  styleUrls: ['./ps-dropdown-product-category.component.scss'],
})
export class PsDropdownProductCategoryComponent extends PsSelectDropdownComponent implements OnInit {

  @Input() options: IOptionsPsDropdownproductCategoriesExposed = {};
  public defaultOptions: IOptionsPsSelectDropdown = {};

  constructor(
    commonService: PsCommonService,
    logger: LoggerService,
    private omniPullService?: OmniPullService
  ) {
    super(commonService, logger);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultOptions, this.options, false);
    this.loadProductCategories();
  }

  /**
   * populating product categories fetched from server
   */
  private async loadProductCategories() {

    const productCategories = await this.omniPullService.returnProductCategories({}).catch(error => {
      this.logger.error('Error: While fetching Product types in PsDropdownProductCategoryComponent : ', error);
    });
    this.defaultOptions.listOfOptions = [];
    if (productCategories && productCategories.gridModel && productCategories.gridModel.length > 0) {
      for (const iterator of productCategories.gridModel) {
        const productCategory: IPsSelect = {
          itemValue: iterator.categoryCode,
          description: iterator.description,
          selectedObj: iterator
        };
        this.defaultOptions.listOfOptions.push(productCategory);
      }
    }
  }

  selectDropDownChange(values: IchangeValues) {
    this.onPsChange.emit(values);
  }
}
